import { Helmet } from 'react-helmet-async';
import PageHeaderOrders from './PageHeaderEad';
import PageTitleWrapper from 'src/components/PageTitleWrapper';
import { Grid, Container } from '@mui/material';
import Devolution from './Ead';
import Ead from './Ead';
import { UserContext } from 'src/context/UserContext';
import React from 'react';
import http from 'src/http';

function ApplicationsTransactions() {


  const {usuario} = React.useContext(UserContext);
  const [devolution, setDevolution] = React.useState(null);

  async function getAllIdPedidos(){
    const {data} = await http.get(`/pedidos/dashboard/${usuario?.id}`);
    setDevolution(data[0].itens[0].id_produto);
  }

  React.useEffect(()=>{
    getAllIdPedidos();
  }, [])

  return (
    <>
      <Helmet>
        <title>Vendas - Pedidos</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeaderOrders />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            <Ead devolution={devolution}/>
          </Grid>
        </Grid>
      </Container>
    </>
  );
}

export default ApplicationsTransactions;
