import { Card, Divider, Table, TableBody, TableRow, TableCell, TableHead, TableContainer, Typography} from '@mui/material';
import { useEffect, useState } from 'react';
import http from 'src/http';
import { IDevolution } from 'src/models/product';
import DevolutionTable from './DevolutionTable';
import React from 'react';
import { UserContext } from 'src/context/UserContext';
import { dataProducts } from 'src/pages/Solutions/Products/DataProducts/dataProducts';
import {AiOutlineArrowRight} from 'react-icons/ai';
import styles from './Ead.module.scss';
import {AiOutlineUserAdd, AiFillDelete, AiOutlineArrowLeft} from 'react-icons/ai'
import {HiOutlineUsers} from 'react-icons/hi'
import { LoadingButton } from '@mui/lab';
import {BiEdit} from 'react-icons/bi'
import { checkContext } from 'src/context/CheckoutContext';


function Ead(devolution: any) {
  

  const [data, setData] = useState([]);
  const [selectProduct, setSelectProduct] = useState(false);
   //User one
   const [nameUser, setNameUser] = useState('');
   const [emailUser, setEmailUser] = useState('');
   const [passwordUser, setPasswordUser] = useState('');
   const [phoneUser, setPhoneUser] = useState('');
 
   const [idUserEad, setIdUserEad] = useState(null);
   const [idCourse, setIdCourse] = useState<number>(null);
   const [idProduct, setIdProduct] = useState(null);
   const [idOrderNest, setIdOrderNest] = useState(null);
   const [usersEad, setUsersEad] = useState([]);
   const [lengthEad, setLengthEad] = useState(null);
 
   const [idEditUser, setIdEditUser] = useState(null);
   const [message, setMessage] = useState(null);
   const [success, setSuccess] = useState(null);
   const [error, setError] = useState(null);
   const [loading, setLoading] = useState(false);
   const [finishedTimeout, setFinishedTimeout] = React.useState(false);
   
   const [editUser, setEditUser] = useState(false);
   const [index, setIndex] = useState(null);

   const [optionTraining, setOptionTraining] = useState("");
 
  const {
    usuario
  } = React.useContext(UserContext);


  async function getAllIdPedidos(){
    const {data} = await http.get(`/pedidos/dashboard/${usuario?.id}`);

    setOptionTraining(data[0].descricao)
    await getAllPedidos(data[0].itens[0].id_produto);
  }

  async function getAllPedidos(id_produto: any){
    const {data} = await http.get(`/produtos/${id_produto}`);
    setData(data);
  }

  
    const getOrderById = async()=>{
      if(usuario?.id !== undefined){
        const {data} = await http.get(`/pedidos/dashboard/${usuario?.id}`);
        setIdOrderNest(data[0].id);
      }
    }
  
    const getOrder = async()=>{
      if(usuario?.id !== undefined){
        const {data} = await http.get(`/pedidos/${idOrderNest}`);
        setIdProduct(data.itens[0].id_produto)
        getIdCourseNest()
      }
    }
  
  
    const getIdCourseNest = ()=>{
      if(devolution.devolution == 4){
        setIdCourse(2)
      }else if(devolution.devolution == 14){
        setIdCourse(11)
      }
      else if(devolution.devolution == 34){
        setIdCourse(9)
      }
      else if(devolution.devolution == 44){
        setIdCourse(7)
      }
      else if(devolution.devolution == 54){
        setIdCourse(8)
      }
      else if(devolution.devolution == 1){
        setIdCourse(2)
      }


    }
  


    const handleSubmitEnrollment = async (id_aluno: number)=>{     
      if(id_aluno && idCourse != null){
        const fetchUser = fetch('https://nestrental.com.br:80/api/ead/student/enrollment', {
          method: 'POST',
          headers:{
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            curso_id: idCourse,
            usuario_id: id_aluno
          })
        })
        const response = await fetchUser;
        const jsonResponse = await response.json();
        
      }

      setTimeout(()=>{
        setSuccess('');
        setError('')
      }, 3000)
    }

  
    const handleSubmitUserOne = async (e: any)=>{
      setLoading(true) 
      e.preventDefault();
      if(usersEad.length == 2){
        setError('Limite de alunos matriculados atingido!');
      }else if(usersEad.length < 2 && devolution != null ){
        const fetchUser = fetch('https://nestrental.com.br:80/api/ead/student', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            "email": emailUser,
            "nome": nameUser,
            "senha": passwordUser,
            "telefone": phoneUser,
            "tipo": 1,
            "personalizado": devolution.devolution,
            "anotacoes": usuario?.id,
            "status": 1
          })
        })
        const response = await fetchUser;
        const jsonResponse = await response.json();
    
        if(jsonResponse.http_status == 200){
          setError("")
           setSuccess("Aluno matriculado com sucesso.")
         
     
        }else if(jsonResponse.http_status == 409){
         
            setError(jsonResponse.message)
            setSuccess("")
        }

        setIdUserEad(jsonResponse.data.aluno_id);
        await handleSubmitEnrollment(jsonResponse.data.aluno_id);
        getCourserByUser();
      }
      setLoading(false);
    }
  
    
    const editeeUser = async (e: any)=>{
      e.preventDefault();
      const fetchUser = fetch(`https://nestrental.com.br:80/api/ead/student/${idEditUser}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          "email": emailUser,
          "nome": nameUser,
          "senha": passwordUser,
          "telefone": phoneUser,
          "tipo": 1,
          "personalizado": usuario?.id,
          "status": 1
        })
      })
      const response = await fetchUser;
      const jsonResponse = await response.json();
      if(jsonResponse.http_status == 200){
        setSuccess("Dados editados com sucesso!");
        setError('')
      }else  if(jsonResponse.http_status == 400){
        setError("Email já cadastrado!")
        setMessage('');
      }
    }

  
    const deletUser = async (e: any, id: number)=>{
      e.preventDefault();
      const fetchUser = fetch(`https://nestrental.com.br:80/api/ead/student/${id}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
      })
      const response = await fetchUser;
      getCourserByUser();
    }
  
  
    const getCourserByUser = async ()=>{
        const fetchUser = fetch(`https://nestrental.com.br:80/api/ead/student/${devolution.devolution}/${usuario?.id}`, {
          method: 'GET',
          headers:{
            'Content-Type': 'application/json'
          },
        })
        const response = await fetchUser;
        const jsonResponse = await response.json()
        setLengthEad(jsonResponse.length)
        setUsersEad(jsonResponse)
    }
  
    const setParamsEditUser = (id: number, index: number)=>{
      setIndex(index);
      setEditUser(true);
      setIdEditUser(id);
    }



  
    useEffect(()=>{
      getOrderById();
      getOrder();
    }, [message || error])

    useEffect(()=>{
      getAllIdPedidos();
      getIdCourseNest();
    }, [devolution.devolution])

    useEffect(()=>{
      getCourserByUser();
    }, [devolution.devolution != null]);
  
  

  return (
    <Card>
      {selectProduct &&
        <React.Fragment>
        <Card className={styles.ead}>
            <div className={styles['ead-form1']}>
              <div className={styles['ead-form2-icon']}>
                <div className={styles['ead-form2-icon-div']}>
                <AiOutlineUserAdd style={{width: '35px', height: '35px'}}/> <h3>Cadastrar Aluno</h3>
    
                </div>
                
                
                <AiOutlineArrowLeft style={{width: '35px', height: '35px', alignSelf: 'left', marginRight: '15px', cursor: 'pointer'}} onClick={(e)=>setSelectProduct(!selectProduct)}/>
              </div>
               
                <form className={styles['ead-form1-form']} onSubmit={editUser? editeeUser : handleSubmitUserOne}>
                  <div className={styles['ead-form1-form-block']}>
                    <label>Nome:</label>
                    <input type="text" id="name_user_ead_one" onChange={(e)=>setNameUser(e.target.value)} defaultValue={editUser? usersEad[index].nome : nameUser}/>
                  </div>
                  <div className={styles['ead-form1-form-block']} >
                    <label>E-mail:</label>
                    <input type="text" id="email_user_ead_one" onChange={(e)=>setEmailUser(e.target.value)}  defaultValue={editUser? usersEad[index].email : emailUser}/>
                  </div>
                  <div className={styles['ead-form1-form-block']}>
                    <label>Telefone:</label>
                    <input type="text" id="phone_user_ead_one" onChange={(e)=>setPhoneUser(e.target.value)}  defaultValue={editUser? usersEad[index].telefone : phoneUser}/>
                  </div>
                  <div className={styles['ead-form1-form-block']}>
                    <label>Senha:</label>
                    <input type="password" id="password_user_ead_one" onChange={(e)=>setPasswordUser(e.target.value)}  defaultValue={editUser? usersEad[index].email : passwordUser}/>
                  </div>
                  <LoadingButton className={styles['ead-form1-button']}  variant='contained' type="submit" loading={loading}>
                  {editUser ? 'Atualizar': 'Cadastrar'}
                  </LoadingButton>
                  {success? <p style={{color: 'green', marginTop: '10px'}}>{success}</p>: <p style={{color: 'red', marginTop: '10px'}}>{error}</p>}
                </form>
            </div>
        </Card>
    
        <Card className={styles.eadList}>
          <div className={styles['eadList-title']}  style={{paddingLeft: '1rem'}}>
            <HiOutlineUsers style={{width: '35px', height: '35px'}}/> <h3>Alunos cadastrados</h3>
          </div>
          <Card>
         
    
          <Divider />
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Usuário</TableCell>
                  <TableCell>Nome</TableCell>
                  <TableCell>Email</TableCell>
                  <TableCell>Telefone</TableCell>
                  <TableCell>Data Cadastro</TableCell>
                  <TableCell>Ações</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {usersEad.length != 0 && usersEad.map((item, index)=>{
                  return (
                    <TableRow key={index}>
                     
                      <TableCell>
                        <Typography
                          variant="body1"
                          fontWeight="bold"
                          color="text.primary"
                          gutterBottom
                          noWrap
                        >
                          {item.username} {index+1}°
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography
                          variant="body1"
                          fontWeight="bold"
                          color="text.primary"
                          gutterBottom
                          noWrap
                        >
                          {item.nome}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography
                          variant="body1"
                          fontWeight="bold"
                          color="text.primary"
                          gutterBottom
                          noWrap
                        >
                             {item.email}
                        </Typography>
                        </TableCell>
                        <TableCell>
                          <Typography 
                              variant="body1"
                              fontWeight="bold"
                              color="text.secondary"
                              gutterBottom 
                              noWrap
                          >
                            {item.telefone}
                          </Typography>
                      </TableCell>
                      <TableCell>
                          <Typography 
                              variant="body1"
                              fontWeight="bold"
                              color="text.secondary"
                              gutterBottom 
                              noWrap
                          >
                            {item.data_cadastro}
                          </Typography>
                      </TableCell>
                      <TableCell style={{textAlign: 'center', display: 'flex', alignItems: 'center', gap: '10px'}}>
                          <BiEdit style={{width: '25px', height: '25px', cursor: 'pointer'}} onClick={(e)=>setParamsEditUser(item.aluno_id, index)}/>
                          <AiFillDelete style={{width:'25px', height: '25px', color: 'red',  cursor: 'pointer'}} onClick={(e)=>deletUser(e, item.aluno_id)}/>
                      </TableCell>
    
                    </TableRow>
                  )
                })
              }
              </TableBody>
            </Table>
          </TableContainer>
        </Card>
        </Card>
    
        </React.Fragment>
  }

    {!selectProduct && optionTraining == 'yes'&&
      <Card>
      <div  style={{paddingLeft: '1rem'}}>
        <h3>Produtos</h3>
      </div>
      <Divider />
      <TableContainer>
        <Table>
          <TableBody>
            {data && data.map((item, index)=>{
              return (
                <TableRow key={index} className={styles.product} onClick={(e)=>setSelectProduct(!selectProduct)}>
                 
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                     {item.nome}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      
                    </Typography>
                  </TableCell>
               
                  <TableCell style={{textAlign: 'right', display: 'flex', alignItems: 'right', flexDirection: 'row', gap: '10px', justifyContent: 'right'}}>
              
                    <AiOutlineArrowRight style={{width: '35px', height: '35px'}}/>

                  </TableCell>

                </TableRow>
              )
            })
          }
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
   
         
    }

    {optionTraining == 'no' || optionTraining == '????' &&
       <Card style={{padding: '1rem'}}><p>Você optou por não fazer treinamento (Curso EAD) para operar esta máquina.</p></Card>
    }
    </Card>
  );
}

export default Ead;
